import usTvShow from "./usTvShowData";
import "./UsTvShow.css";

const UsTvShow = () => {
  return (
    <>
      <h4>US TV Shows</h4>
      <div className="us__tv__shows__section">
        {usTvShow.map((items, id) => {
          return (
            <div key={id}>
              <img src={items} alt="" />
            </div>
          );
        })}
      </div>
    </>
  );
};

export default UsTvShow;
