import releaseLatest from "./releaseLatestData";
import "./ReleaseLatest.css";

const ReleaseLatest = () => {
  return (
    <>
      <h4>Released in the Past Year</h4>
      <div className="release__latest__section">
        {releaseLatest.map((items, id) => {
          return (
            <div key={id}>
              <img src={items} alt="release__latest__images" />
            </div>
          );
        })}
      </div>
    </>
  );
};

export default ReleaseLatest;
