import realStories from "./realStoriesData";
import "./RealStories.css";

const RealStories = () => {
  return (
    <>
      <h4>Real Stories</h4>
      <div className="real__stories__section">
        {realStories.map((items, id) => {
          return (
            <div key={id}>
              <img src={items} alt="" />
            </div>
          );
        })}
      </div>
    </>
  );
};

export default RealStories;
