import continueWatch from "./continueWatchData";
import "./ContinueWatch.css";

const ContinueWatch = () => {
  return (
    <>
      <h4>Continue Watching for Laura</h4>
      <div className="continue__watch__section">
        {continueWatch.map((items, id) => {
          return (
            <div key={id}>
              <img src={items} alt="" />
            </div>
          );
        })}
      </div>
    </>
  );
};

export default ContinueWatch;
