import trendingNow from "./trendingNowData";
import "./TrendingNow.css";
import React from "react";

const TrendingNow = () => {
  return (
    <React.Fragment>
      <h4>Trending Now</h4>
      <div className="trending__now__section">
        {trendingNow.map((items, id) => {
          return (
            <div key={id}>
              <img src={items} alt="" />
            </div>
          );
        })}
      </div>
    </React.Fragment>
  );
};

export default TrendingNow;
