import animation from "./animationData";
import "./Animation.css";

const Animation = () => {
  return (
    <>
      <h4>Animation</h4>
      <div className="animation__section">
        {animation.map((items, id) => {
          return (
            <div key={id}>
              <img src={items} alt="" />
            </div>
          );
        })}
      </div>
    </>
  );
};

export default Animation;
