import tvShows from "./tvShowsData";
import "./TvShows.css";
import React from "react";

const TvShows = () => {
  return (
    <React.Fragment>
      <h4>TV Shows</h4>
      <div className="tv__shows__section">
        {tvShows.map((items, id) => {
          return (
            <div key={id}>
              <img src={items} alt="" />
            </div>
          );
        })}
      </div>
    </React.Fragment>
  );
};

export default TvShows;
